html, body {
  min-height: 100%;
}
body, div, form, input, select, p { 
  padding: 0;
  margin: 0;
  outline: none;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  color: #eee;
}

h1, h2 {
  text-transform: uppercase;
  font-weight: 400;
}
h2 {
  margin: 0 0 0 8px;
}
.main-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 100px 25px 100px 25px;
  background: rgba(200, 0, 0, 0.5); 
  min-height: 100%
}
.left-part, form {
  padding: 25px;
}
.left-part {
  text-align: center;
}
.fa-graduation-cap {
  font-size: 72px;
}
form {
  background: rgba(0, 0, 0, 0.7); 
}
.title {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
input {
  width: 70%;
  padding: 5px;
  margin-bottom: 30px;
  background: transparent;
  border: none;
  border-bottom: 1px solid #eee;
}
input::placeholder {
  color: #eee;
}
button {
  padding: 10px 5px;
  margin-top: 20px;
  border-radius: 5px; 
  border: none;
  background: #26a9e0; 
  text-decoration: none;
  font-size: 15px;
  font-weight: 400;
  color: #fff;
}

button {
  width: 100%;
}
.btn10{
  width: 10% !important
}
button:hover {
  background: #85d6de;
}
@media (min-width: 568px) {
  html, body {
    height: 100%;
  }
  .main-block {
    flex-direction: row;
    height: calc(100% - 50px);
  }
  .left-part, form {
    flex: 1;
    height: auto;
  }
}